<template>
	<div class="upload_div">
		<el-upload class="upload-demo" ref="upload" :on-exceed="handleExceed" drag :on-success="onSuccess" :on-error="onError" :action="uploadUrl"
		 :file-list="fileList" :limit="1" :auto-upload="true" :multiple="false" :data="filedata">
			<i class="el-icon-upload"></i>
			<div class="el-upload__text">{{$t('common.dragTip')}}<em>{{$t('common.clickUpload')}}</em></div>
		</el-upload>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				uploadUrl: 'uploadFiles',
				fileList: [],
				filedata: {
					user_name: localStorage.getItem('user_name'),
					user_group_power: localStorage.getItem('user_group_power'),
				}
			}
		},
		methods: {
			onSuccess(response, file, fileList) {
				if (response.result) {
					this.uploadVisible = false
					this.$message.success(this.$t('common.uploadSuccess'))
					this.$emit('reGetFile')
				} else {
					this.$message({
						showClose: true,
						message: response.msg,
						type: 'error'
					})
				}
			},
			onError(error, file, fileList) {
				this.$message.error(this.$t('common.uploadFailed'))
			},
			handleExceed(files, fileList) {
			        this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
			      },
		}
	}
</script>

<style>
	.upload_div {		
		width:360px;
		margin:0 auto;
		margin-top: 40px;
	}
</style>
